import localization from '../../public/locales/cs/application.json';
import { useSign } from '../components/Sign/useSign';
import {
  CheckResultOfSignProductIdEnum,
  useAPIs,
  useGetPaymentOrderDetail,
} from '@flexi-monorepo/primary-api';
import {
  DynamicConfig,
  getGlobalConfig,
  getYYYYMMDD,
  useAuth,
} from '@flexi-monorepo/shared';
import {
  Animation,
  Button,
  Card,
  CardFooter,
  CardGroup,
  Col,
  Paragraph,
  Row,
  Spinner,
  StatusInfo,
} from '@george-labs.com/design-system';
import error from '@george-labs.com/design-system/assets/animations/error.json';
import success from '@george-labs.com/design-system/assets/animations/success.json';
import { QRCodeCanvas } from 'qrcode.react';
import { useEffect, useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';

const { signPage } = localization;

export enum SignStatus {
  ERROR = 'ERROR',
  SIGNED = 'SIGNED',
  TO_BE_SIGNED = 'TOBESIGNED',
}

export enum OfferSignStatusEnum {
  SIGNED = 'SIGNED',
  REJECTED = 'REJECTED',
  CANCELLED = 'CANCELLED',
  IN_PROGRESS = 'IN_PROGRESS',
  EXPIRED = 'EXPIRED',
  NOT_STARTED = 'NOT_STARTED',
  TO_BE_SIGNED = 'TOBESIGNED',
}

export const Sign = () => {
  const config: DynamicConfig = getGlobalConfig();
  const { token } = useAuth(config);
  const { t } = useTranslation(['application']);
  const [loading, setLoading] = useState<boolean>(true);
  const [signStatus, setSignStatus] = useState<SignStatus>();
  const urlParams = new URLSearchParams(window.location.search);
  const productId = urlParams.get(
    'productId'
  ) as CheckResultOfSignProductIdEnum;
  const offerId = parseInt(urlParams.get('offerId') || '0', 10);
  const { offerApi } = useAPIs();
  const { signOtherMethod, loadingRedirect } = useSign();
  const paymentInfo = useGetPaymentOrderDetail(
    {
      productId: productId ?? CheckResultOfSignProductIdEnum.K1Fn,
      offerId,
    },
    undefined,
    { enabled: SignStatus.SIGNED === signStatus }
  );
  useEffect(() => {
    let counter = 1;
    const maxAttempts = 80;

    // If productId is not set, we need to use K1Fn as default
    const getProductId: CheckResultOfSignProductIdEnum =
      productId ?? CheckResultOfSignProductIdEnum.K1Fn;
    const checkSignStatus = async () => {
      const signPoll: any = await offerApi.checkResultOfSign({
        offerId,
        productId: getProductId,
      });
      const currentState = signPoll.state;
      if (
        currentState === OfferSignStatusEnum.SIGNED ||
        currentState === OfferSignStatusEnum.TO_BE_SIGNED
      ) {
        setLoading(false);
        setSignStatus(currentState);
        return;
      } else if (
        currentState === OfferSignStatusEnum.CANCELLED ||
        currentState === OfferSignStatusEnum.EXPIRED ||
        currentState === OfferSignStatusEnum.REJECTED ||
        counter >= maxAttempts
      ) {
        setLoading(false);
        setSignStatus(SignStatus.ERROR);
        return;
      }

      counter++;
      setTimeout(checkSignStatus, 1000);
    };

    checkSignStatus();
  }, []);

  const renderSignedStatus = () => {
    if (!paymentInfo || paymentInfo.isLoading) return renderLoadingStatus();
    if (!paymentInfo.isSuccess) return renderErrorStatus();
    const { number, prefix, bankCode } = paymentInfo.data.receiver;
    const { variableSymbol, constantSymbol } = paymentInfo.data.symbols;
    const amount = paymentInfo.data.amount.value;
    const firstPaymentDate = getYYYYMMDD(
      new Date(paymentInfo.data.execution.firstDate)
    );
    const lastPaymentDate = getYYYYMMDD(
      new Date(paymentInfo.data.execution.lastDate)
    );

    const prefixDisplayedValue =
      prefix && prefix !== '000000' ? `${prefix}-` : '';
    const accountNumberFull = `${prefixDisplayedValue}${number}/${bankCode}`;

    const qrData =
      `SPD*1.0` +
      `*ACC:CZ5708000000000002226222` +
      `*AM:${amount}.00` +
      `*CC:CZK` +
      `*FRQ:1M` +
      `*DT:${firstPaymentDate}` +
      `*DL:${lastPaymentDate}` +
      `*DH:1` +
      `*X-VS:${variableSymbol}` +
      `*X-KS:${constantSymbol}` +
      `*MSG:ZIVOTNI POJISTENI`;
    return (
      <StatusInfo
        variant={StatusInfo.VARIANT.SUCCESS}
        title={signPage.signed.description}
        data-cy="success"
        animation={
          <Animation
            size={Animation.SIZE.SIZE_128}
            autoplay={true}
            loop={false}
            color={Animation.COLOR.GREEN_100}
            delay={200}
            data={success}
          />
        }
      >
        <Row noGutters={true}>
          <Card className="offset-md-2 col-md-8">
            <Row noGutters={true}>
              <Col md={4} style={{ minWidth: '160px', paddingTop: '0.75rem' }}>
                <QRCodeCanvas
                  value={qrData}
                  size={128}
                  bgColor={'#ffffff'}
                  fgColor={'#000000'}
                  level={'M'}
                  includeMargin={false}
                />
              </Col>
              <Col md={3}>
                <Paragraph style={labelStyle}>
                  {t('signPage.payment.accountNumber')}
                </Paragraph>
                <Paragraph style={valueStyle}>{accountNumberFull}</Paragraph>
                <Paragraph style={labelStyle}>
                  {t('signPage.payment.constantSymbol')}
                </Paragraph>
                <Paragraph style={valueStyle}>{constantSymbol}</Paragraph>
              </Col>
              <Col md={4}>
                <Paragraph style={labelStyle}>
                  {t('signPage.payment.variableSymbol')}
                </Paragraph>
                <Paragraph style={valueStyle}>{variableSymbol}</Paragraph>
                <Paragraph style={labelStyle}>
                  {t('signPage.payment.amount')}
                </Paragraph>
                <Paragraph style={amountStyle}>
                  {/* eslint-disable-next-line i18next/no-literal-string */}
                  <b>{amount} K</b> msn
                </Paragraph>
              </Col>
            </Row>
          </Card>
        </Row>
      </StatusInfo>
    );
  };

  const renderToBeSignedStatus = () => (
    <StatusInfo
      variant={StatusInfo.VARIANT.SUCCESS}
      title={signPage.toBeSigned.title}
      data-cy="to-sign"
      animation={
        <Animation
          size={Animation.SIZE.SIZE_128}
          autoplay={true}
          loop={false}
          color={Animation.COLOR.GREEN_100}
          delay={200}
          data={success}
        />
      }
    >
      <Paragraph>
        <Trans t={t} i18nKey={signPage.toBeSigned.description}></Trans>
      </Paragraph>
    </StatusInfo>
  );

  const renderErrorStatus = () => (
    <StatusInfo
      variant={StatusInfo.VARIANT.ERROR}
      title={signPage.notSigned.title}
      data-cy="chyba"
      animation={
        <Animation
          size={Animation.SIZE.SIZE_128}
          autoplay={true}
          loop={false}
          color={Animation.COLOR.RED_100}
          delay={200}
          data={error}
        />
      }
    >
      <Paragraph>
        <Trans t={t} i18nKey={signPage.notSigned.description}></Trans>
      </Paragraph>
    </StatusInfo>
  );

  const renderLoadingStatus = () => (
    <div className={'py-5 text-center'}>
      <Spinner
        data-cy="spinner"
        size={Spinner.SIZE.LARGE}
        center={true}
        className={'mb-3'}
      ></Spinner>
      <Paragraph>
        <Trans t={t} i18nKey={signPage.loading.description}></Trans>
      </Paragraph>
    </div>
  );

  return (
    <CardGroup className={'mx-4'}>
      <Card>
        {!loading && signStatus === SignStatus.SIGNED && renderSignedStatus()}
        {!loading &&
          signStatus === SignStatus.TO_BE_SIGNED &&
          renderToBeSignedStatus()}
        {!loading && signStatus === SignStatus.ERROR && renderErrorStatus()}
        {loading && renderLoadingStatus()}
      </Card>
      {!loading && (
        <CardFooter>
          {(signStatus === SignStatus.SIGNED ||
            signStatus === SignStatus.TO_BE_SIGNED) && (
            <Button
              data-cy={'btn-dokonit'}
              variant={Button.VARIANT.PRIMARY}
              onClick={() => (window.location.href = 'cshub://cshubappquit')}
            >
              <Trans t={t} i18nKey={signPage.signed.firstButtonText}></Trans>
            </Button>
          )}
          {signStatus === SignStatus.ERROR && (
            <>
              <Button
                data-cy={'btn-zrusit'}
                className={'mr-3 mb-3 mb-sm-0'}
                variant={Button.VARIANT.SECONDARY}
                onClick={() => (window.location.href = 'cshub://cshubappquit')}
              >
                <Trans
                  t={t}
                  i18nKey={signPage.notSigned.secondButtonText}
                ></Trans>
              </Button>
              <Button
                isLoading={loadingRedirect}
                data-cy={'btn-sign-variant'}
                variant={Button.VARIANT.PRIMARY}
                disabled={!token || loadingRedirect}
                onClick={
                  token
                    ? () =>
                        signOtherMethod(
                          Number(offerId),
                          productId as CheckResultOfSignProductIdEnum,
                          config,
                          token
                        )
                    : undefined
                }
              >
                <Trans
                  t={t}
                  i18nKey={signPage.notSigned.firstButtonText}
                ></Trans>
              </Button>
            </>
          )}
        </CardFooter>
      )}
    </CardGroup>
  );
};

const labelStyle = {
  fontWeight: 400,
  fontSize: '14px',
  paddingTop: '0.75rem',
  paddingBottom: 0,
};
const valueStyle = {
  fontSize: '16px',
  lineHeight: '24px',
  fontWeight: 'bold',
  paddingTop: 0,
};
const amountStyle = {
  fontSize: '16px',
  lineHeight: '24px',
  color: '#199CF9',
  paddingTop: 0,
};
export default Sign;
