import {
  SignOfferChannelEnum,
  SignOfferProductIdEnum,
  useAPIs,
} from '@flexi-monorepo/primary-api';
import { DynamicConfig } from '@flexi-monorepo/shared';
import { useState } from 'react';

export function useSign() {
  const { offerApi, bankerApi } = useAPIs();
  const [loadingRedirect, setLoadingRedirect] = useState(false);

  const signOtherMethod = async (
    offerId: number,
    productId: string,
    config: DynamicConfig | undefined,
    token: string
  ) => {
    const baseUrl = window.location.origin;
    const returnUrl = window.btoa(
      `${baseUrl}/novaflexi/sign?productId=${SignOfferProductIdEnum.K1Fn}&offerId=${offerId}#access_token={tokenx}`
    );
    setLoadingRedirect(true);

    try {
      const banker = await bankerApi.getBanker();
      const branchId = await getBranchId(banker);
      const response = await offerApi.signOffer({
        productId: SignOfferProductIdEnum.K1Fn,
        branch: branchId,
        offerId,
        channel: SignOfferChannelEnum.Tablet,
      });

      redirectToSign(response.id, offerId, branchId, returnUrl, token, config);
    } catch (e) {
      console.error(e);
    } finally {
      setLoadingRedirect(false);
    }
  };

  const getBranchId = async (banker: any) => {
    if (banker?.branch) {
      return banker.branch;
    }

    const branches = await bankerApi.getBranches();
    if (branches?.branches?.length) {
      return branches.branches[0].id;
    }

    throw new Error('No branch found');
  };

  return {
    signOtherMethod,
    loadingRedirect,
  };
}

function redirectToSign(
  bufferId: string,
  offerId: number,
  branchId: string | undefined,
  returnUrl: string,
  token: string,
  config: DynamicConfig | undefined
): void {
  const isDesktop = token?.startsWith('3/') || token?.startsWith('adfs_');
  const applicationId = isDesktop ? 'FLEXI' : 'FLEXI_T';
  const signUrl = isDesktop ? config?.signUrlDesktop : config?.signUrlTablet;

  const urlParams = new URLSearchParams({
    applicationId,
    bufferId,
    productId: SignOfferProductIdEnum.K1Fn,
    offerId: offerId.toString(),
  });

  if (branchId) urlParams.append('branchId', branchId);
  if (!isDesktop) urlParams.append('returnURL', returnUrl);

  window.location.href = `${signUrl}?${urlParams.toString()}${
    isDesktop ? '' : `#access_token={tokenx}`
  }`;
}
